<!--
 * @Author: TinaHuang0183
 * @Date: 2022-07-22 11:33:28
 * @LastEditors: cumany cuman@qq.com
 * @LastEditTime: 2023-02-17 19:20:32
 * @FilePath: /notion-widgets-site/src/components/Layouts-v2.vue
 * @Description: 这是默认设置,请设置`customMade`, 打开koroFileHeader查看配置 进行设置: https://github.com/OBKoro1/koro1FileHeader/wiki/%E9%85%8D%E7%BD%AE
-->
<template>
  <div class="layout">
    <img class="circle" src="@/assets/home-bg.svg" v-show="!show_header" />
    <div class="header" v-if="show_header">
      <div class="site">
        <img id="icon" src="@/assets/icon.svg" />
        <router-link :to="{ name: 'index' }" :underline="false"
          ><h2>{{ site_name }}</h2></router-link
        >
        <!-- <el-tag
          ><i class="el-icon-view"></i> <span id="busuanzi_value_site_pv"></span
        ></el-tag>
        <el-tag
          ><i class="el-icon-user"></i> <span id="busuanzi_value_site_uv"></span
        ></el-tag> -->
      </div>
      <div class="extra">
        <router-link
          class="header-link"
          :class="{ active: active_tab === 'plugin' }"
          :to="{ name: 'lib' }"
          >组件</router-link
        >

        <a class="header-link" type="info" @click="handleDoc">使用教程</a>
     
        <router-link
          class="header-link"
          :class="{ active: active_tab === 'releases' }"
          :to="{ name: 'log' }"
          >更新日志</router-link
        >
        <router-link :to="{ name: 'lib' }" :underline="false"
          ><el-button type="primary" plain round
            >使用组件</el-button
          ></router-link
        >
      </div>
    </div>
    <div class="header2" v-else>
      <h1>{{ site_name }}</h1>
      <div class="menu">
        <div class="menu-item">首页</div>
        <div class="menu-item"><router-link to="/lib">组件</router-link></div>

        <div class="menu-item" @click="handleDoc">使用教程</div>
        <div class="menu-item">
          <router-link to="/releases">日志</router-link>
        </div>
        <!-- <div class="menu-item">
          <el-badge value="new" class="item">
            <router-link to="/news"
              ><div class="menu-item">公告</div></router-link
            >
          </el-badge>
        </div> -->
      </div>
    </div>
    <slot></slot>
    <div class="footer">
      <div class="inner">
        <span>© 2022 </span>
      </div>
    </div>
    <!-- drawer -->
    <el-drawer
      title="使用教程"
      :visible.sync="drawer"
      :direction="direction"
      size="50%"
    >
      <document />
    </el-drawer>
    <!-- qustion -->

    <!-- news -->
    <el-dialog
      :title="newUpdate && newUpdate.title"
      :visible.sync="centerDialogVisible"
      width="40%"
      center
    >
      <div class="new-box" v-html="newUpdate && newUpdate.msg"></div>
      <p class="dialog-date">{{ newUpdate && newUpdate.datetime }}</p>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="handleUpdateDialog(newUpdate)"
          >立即体验</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>
<script>
import Document from '../views/Document.vue'
export default {
  components: { Document },
  data () {
    return {
      site_name: 'Widgets',
      drawer: false,
      direction: 'rtl',
      newUpdate: null,
      centerDialogVisible: false,
      pathname: this.$route.name
      // news_update:
    }
  },
  computed: {
    show_header () {
      let _path = this.$route.name
      console.log(_path)
      return _path !== 'index'
    },
    active_tab () {
      let _path = this.$route.path
      if (_path.includes('/lib') || _path.includes('/create')) {
        return 'plugin'
      } else if (_path.includes('/releases')) {
        return 'releases'
      }
      return _path
    }
  },
  mounted () {
    this.getDatas()
  },
  methods: {
    handleDoc () {
      this.drawer = true
    },

    getDatas () {
      const _this = this
      // this.$axios.get(`./news.json?v=${Math.random()}`).then((res) => {
      //   // eslint-disable-next-line one-var
      //   let list = res.data.news,
      //     // eslint-disable-next-line camelcase
      //     show_update = localStorage.update
      //   for (let i in list) {
      //     if (list[i].show && list[i].type === 'update') {
      //       _this.newUpdate = list[i]
      //       break
      //     }
      //   }
      //   if (!show_update || show_update !== _this.newUpdate.datetime) {
      //     _this.centerDialogVisible = true
      //     localStorage.update = _this.newUpdate.datetime
      //   }
      // })
    },

    handleUpdateDialog (item) {
      if (item.page) {
        this.$router.push(item.page)
        this.centerDialogVisible = false
      }
      return (this.centerDialogVisible = false)
    }
  }
}
</script>
<style lang="less" scoped>
@import "../less/var.less";
.layout {
  position: relative;
  // z-index: 1;
  overflow-x: hidden;

  .circle {
    width: 100%;
    right: 0;
    display: block;
    position: absolute;
    z-index: 0;

  }
}
.header {
  position: fixed;
  left: 0;
  right: 0;
  padding: 15px 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #ffffff33;
  backdrop-filter: blur(15px);
  z-index: 1000;
  .site {
    display: flex;
    align-items: center;
    #icon {
      width: 40px;
      height: 40px;
      margin-right: 15px;
    }
    h2 {
      margin: 0;
      font-size: 22px;
    }
    .el-tag {
      margin-left: 10px;
    }
  }
  .header-link {
    margin-right: 20px;
    font-size: 18px;
    padding: 5px 10px;
    cursor: pointer;
    &.active {
      background: @primary-color;
      // border-bottom: 1px solid @primary-color;
      color: #ffffff;
      border-radius: 35px;
    }
  }
}
.header2 {
  color: #ffffff;
  position: relative;
  z-index: 1;
  h1 {
    margin: 0;
    font-size: 0.8rem;
    // width: 5rem;
    padding: 0.8rem;
    text-align: center;
    // border-bottom: 0.1rem double black;
  }
  .menu {
    width: 60vw;
    margin: 0 auto;
    font-size: 0.5rem;
    display: flex;
    justify-content: space-between;
    &-item {
      cursor: pointer;
    }
  }
  .menu .menu-item:first-child
  {
    color: #333;
  }
}
.footer {
  padding: 0 30px;
  text-align: center;
  .inner {
    border-top: 1px solid #f3f3f3;
    padding: 30px;
    font-size: 14px;
    span {
      display: inline-block;
      margin: 0 15px;
    }
  }
}
.feedback {
  display: block;
  position: fixed;
  width: 0.8rem;
  height: 0.8rem;
  border-radius: 50%;
  font-size: 0.4rem;
  text-align: center;
  line-height: 0.8rem;
  right: 10px;
  bottom: 30px;
  background: #ffffff;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 10%);
  cursor: pointer;
  font-weight: bold;
  z-index: 1000;
}
.dialog-date {
  text-align: right;
}
.new-box {
  ::v-deep img {
    max-width: 100%;
    display: block;
  }
  ::v-deep p {
    line-height: 1.5;
    font-size: 15px;
  }
}
</style>



